
.navbar{
    background: #ffa500;
    height: 80px;
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-container{
    display: flex;
    justify-content: space-between;
    height: 80px;
}

.container{
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
}

.navbar-logo{
    color: black;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-item{
    height: 80px;
}

.nav-links{
    color:black;
    display:flex;
    align-items: center;
    text-decoration:none;
    padding:0.5rem 1rem;
    height: 100%;
}

.nav-link:hover{
    border-bottom: 4px solid white;
    transition: all 0.2s ease-out;
}

.fa-bars{
    color: white;
}

.nav-links-mobile{
    display:none;
}

.menu-icon{
    display: none;
}

@media screen and (max-width: 960px){

    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active{
        background: #242222;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }
    
    .nav-link:hover{
        background-color: white;
        color:#242424;
        border-radius:0;
    }

    .navbar-logo{
       position: absolute;
       top: 0;
       left: 0;
       transform: translate(25%,50%);
    }

    .menu-icon{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%,60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
    
    .nav-links-mobile{
        display:block;
        text-align:center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: black;
        padding: 14px 20px;
        border: 1px solid  white;
        transition: all 0.3s ease-out;
    }

    .nav-links-mobile:hover{
        background: white;
        color: #242424;
        transition: 250ms;
    }

    .fa-times{
        color: white;
        font-size: 2rem;
    }
    



}


